@import url("https://hangeul.pstatic.net/hangeul_static/css/nanum-barun-gothic.css");
* { font-family:'NanumBarunGothic' !important; box-sizing:border-box;}
html, body {
    font-size:10px;
}

@mixin button-reset() {
    border:0;
    background:transparent;
    cursor:pointer;
}

@mixin text-cut() {
    display:block;
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
}
